import "./Heading.css";

export const createHeading = ({
  size = 1,
  backgroundColor = backgroundColor,
  label = label,
  onClick,
}) => {
  const btn = document.createElement("h${size}");
  btn.className = [`btn--${size}`];
  btn.style.backgroundColor = backgroundColor;
  btn.innerText = label;
  btn.addEventListener("click", onClick);

  return btn;
};
